/* Cool infinite background scrolling animation.
 * Twitter: @kootoopas
 */


/* Background data (Original source: https://subtlepatterns.com/grid-me/) */

$bg-url: "data:image/png;base64,";
$bg-width: 500px;
$bg-height: 499px;

/* Animations */
@-webkit-keyframes bg-scrolling-reverse {
  100% { background-position: $bg-width $bg-height; }
}
@-moz-keyframes    bg-scrolling-reverse {
  100% { background-position: $bg-width $bg-height; }
}
@-o-keyframes      bg-scrolling-reverse {
  100% { background-position: $bg-width $bg-height; }
}
@keyframes         bg-scrolling-reverse {
  100% { background-position: $bg-width $bg-height; }
}

@-webkit-keyframes bg-scrolling {
  0% { background-position: $bg-width $bg-height; }
}
@-moz-keyframes    bg-scrolling {
  0% { background-position: $bg-width $bg-height; }
}
@-o-keyframes      bg-scrolling {
  0% { background-position: $bg-width $bg-height; }
}
@keyframes         bg-scrolling {
  0% { background-position: $bg-width $bg-height; }
}

/* Main styles */
.body {
    height: 100vh;
    width: 100vw;
    display: flex;
  
  color: #999;
  /* img size is 400x400 */
  background: url(../photos/cartographer.png) repeat 0 0;
  
  -webkit-animation: bg-scrolling-reverse 8s infinite; /* Safari 4+ */
  -moz-animation:    bg-scrolling-reverse 8s infinite; /* Fx 5+ */
  -o-animation:      bg-scrolling-reverse 8s infinite; /* Opera 12+ */
  animation:         bg-scrolling-reverse 8s infinite; /* IE 10+ */
  -webkit-animation-timing-function: linear;
  -moz-animation-timing-function:    linear;
  -o-animation-timing-function:      linear;
  animation-timing-function:         linear;

}
